import {Component} from "@angular/core";
import {NavController, Events} from "ionic-angular";
import {TripService} from "../../services/trip-service";
import {CheckoutTripPage} from "../checkout-trip/checkout-trip";
import { Storage } from "@ionic/storage";
import { ActivityService } from "../../services/activity-service";
import {Validators, FormBuilder, FormGroup, FormControl } from '@angular/forms';

@Component({
  selector: 'page-report-comment',
  templateUrl: 'report-comment.html'
})
export class ReportCommentPage {
  // trip info
  public trip: any;
  form;
  // number of adult
  public adults = 2;
  // number of children
  public children = 0;
  public post:any;

  constructor(public nav: NavController, public tripService: TripService, public act: ActivityService, public storage : Storage, public event : Events) {
    // set sample data
    this.trip = tripService.getItem(1);
    this.post = act.getData();

    this.form = new FormGroup({
      caption: new FormControl('',[Validators.required])
    });
    
    // this.storage.get('post').then((e) => {
    //   console.log(e);
    //   this.post = JSON.parse(e);
     
    // });
    // setTimeout(function(){
    //   event.subscribe('post:data', (post, time) => {
    //     // user and time are the same arguments passed in `events.publish(user, time)`
    //    this.post = post;
        
    //   });
    // },2000);
  
    console.log('item: ',this.post);
    console.log('TRIP: ',this.trip);
  }

  // minus adult when click minus button
  minusAdult() {
    this.adults--;
  }
  ionViewWillEnter(){
 
  }

  // plus adult when click plus button
  plusAdult() {
    this.adults++;
  }

  // minus children when click minus button
  minusChildren() {
    this.children--;
  }

  // plus children when click plus button
  plusChildren() {
    this.children++;
  }

  // go to checkout page
  checkout() {
    this.nav.push(CheckoutTripPage);
  }

  ionViewCanEnter(){
 

   
  }
}



